// RandomText
const tagcl = document.querySelectorAll('.tagcl');
var FontSizes = ["Font_Size_1", "Font_Size_2", "Font_Size_3", "Font_Size_4", "Font_Size_5"];
var Color = ["Color_1", "Color_2", "Color_3", "Color_4", "Color_5"]


for (var i = 0; i < tagcl.length; i++) {
    var RandomFontClass = FontSizes[Math.floor(Math.random() * FontSizes.length)]
    var RandomColor = Color[Math.floor(Math.random() * Color.length)]
    tagcl[i].classList.add(RandomFontClass)
    tagcl[i].classList.add(RandomColor)
}


// Bonbon Background
var BonbonAnzahl = 20;

var TopPosition = 0;
var AnimationDuration = 1;

const Bonbon = document.querySelectorAll('Bonbon');
const timer = ms => new Promise(res => setTimeout(res, ms))

async function load () { 
for (var a = 0; a < BonbonAnzahl; a++) {
    const backgroundstyle = document.getElementsByClassName("backgroundstyle");
    var imagetag = document.createElement("img");
    var RandomHueNumber = Math.random() * (360 - 0) + 0;


    imagetag.src = "./IMG/SVG/BonbonPixelart.svg";
    imagetag.classList.add("Bonbon", "ColorBonbon1")
    imagetag.style.top = 10+TopPosition + "rem"
    imagetag.style.animationDuration = 1 + AnimationDuration + "s"
    imagetag.style.filter = "hue-rotate(" + RandomHueNumber + "deg)";
    backgroundstyle[0].appendChild(imagetag);
    TopPosition+=10;
    AnimationDuration += 0.5;
    console.log(RandomHueNumber);
    await timer(30);
}
}
load();
